.adminheader{
    width:100%;
    background-color: black;
    color:White;
    height:30px;
    padding:5px;
    box-sizing: border-box;

}

.adminbody{
    background-color: aquamarine;
    height:100%;
    background-image: url("./images/admin.png");
    background-size:cover;
}

.admincontrols{
    height:60px;
    padding:10px;
    text-align: center;

}
.createbtn{
    width:150px;
    height:50px;
    border-radius:100px;
    background-color: lightgreen;
    box-shadow: 0 0 10px #ccc;
    border:none;
    outline:none;


}
.mycan{
    width:200px;
    height:auto;
    font:smaller;
    padding:1px;
    background-color:lightgreen;
    margin:auto;
    margin-top:5px;
    border-radius: 100px;
}
.createbtn:hover{
    box-shadow: 0 0 10px green;
}
.admincontent{
    height:auto;

    justify-content: space-around;
   
   margin-top:-10px;
}
.admincontent h3{
    background-color:black;
    color:white;
    padding:10px;
    margin-bottom:0;
    text-align: center;

}







.app
{
    height:100vh;
    font-family:'Times New Roman', Times, serif;
}
.backdrop{
    height:100%;
    width:100%;
    background-color: black;
    position: absolute;
    z-index:3;
    opacity:0.8;
}
.admincontainer{
    font-family: sans-serif;
    background-color:white;
    width:500px;
    height:300px;
    left:50%;
    margin-left:-250px;
    border-radius: 10px;
    overflow:hidden;
    box-shadow:0 0 8px white;
    top:50%;
    margin-top:-150px;
    z-index:4;
    position: absolute;
    display:flex;
    
}
.logbtn{
    justify-content: space-evenly;
    border-bottom: 1px solid black;
}
.form{
    justify-content: space-evenly;
    text-align: center;
}

.form input{
    width:90%;
    margin:auto;
    border:none;
    border-bottom:1px solid black;
    padding:10px;
    display:block;
    transition:all 0.3s;
    outline:none;
    margin-top:30px;
    height:20px;
}
.activebutton{
    background-color: greenyellow;
    
}
.form input:focus{
    height: 25px;
    transition:height 0.3s;
    border-bottom:2px solid greenyellow;
}
.form button{
    margin-top:30px;
    width:100px;
    padding:10px;
    border-radius:100px;
    outline:none;
    border:none;
    background-color: greenyellow;
    box-shadow: 0 0 7px black;
    
}
.form button:hover{
  box-shadow: 0 0 10px green;
    
}
.adminform{
    padding:10px;
    width:50%;
}
.logbtn button{
    width:50%;
    height:30px;
    border:none;
    outline:none;
    background-color:white;
    
}
.logbtn button:hover{
    background-color:greenyellow;
}
.adminpic{
    width:50%;
    background-color:white;
    background-image: url("./images/adminpic.jpg");
    background-size:cover ;
    background-repeat: no-repeat;
}
.candidatepic{
    width:50%;
    background-color:white;
    background-image: url("./images/candidate.jpg");
    background-size:cover ;
    background-repeat: no-repeat;
}
.home{
    height:100vh;
    width:100%;
    background-image: url("./images/home.jpg");
    background-size:cover;
    display: block;
    
    
}
.mybtn{
    position: absolute;
    bottom:0;
    z-index:2;
    width:600px;
    left:50%;
    margin-left:-300px;
    display:flex;
    justify-items: center;
    padding-bottom: 20px;
    justify-content: space-evenly;
}
.mybtn button{
    width:150px;
    background-color: rgb(50, 102, 104);
    border-radius: 100px;
    outline:none;
    border:2px solid white;
}
.mybtn button p{
    margin-top:-10px;
    color:white;
}
.mybtn button h3{
    color:white;
}
.homehead{
    height:12%;
    width:100%;
    background-color: black;
    
    opacity:0.5;

}
.text{
    width:350px;
    left:50%;
   font-family: 'Times New Roman', Times, serif; 
    margin-left:-175px;
    margin-top:-60px;
    overflow:hidden;
    color:white;
    font-size: xx-large;
    position: absolute;
    z-index:2;
}
.footer{
    height:15%;
    width:100%;
    background-color: black;
    position: absolute;
    bottom:0;
    opacity:0.5;

}

.result{
    width:700px;
    height:auto;
    background-color: white;
    position: absolute;
    box-shadow: 0 0 10px greenyellow;
    left:50%;
    margin-left:-350px;
    top:50%;
    margin-top:-250px;
    z-index:5;
    justify-items: center;
    justify-content: baseline;
    padding:30px;
    justify-content: space-evenly;

}
.resultt{
    width:700px;
    height:auto;
    background-color: white;
    position: absolute;
    box-shadow: 0 0 20px white;
    left:50%;
    margin-left:-350px;
    top:50%;
    margin-top:-250px;
    z-index:5;
    justify-items: center;
    text-align: center;
    justify-content: baseline;
    padding:30px;
    justify-content: space-evenly;

}
.resultt p{
    margin:auto;
    margin-top:10px;
    box-shadow: 0 0 6px red;
    text-align: center;
    padding:5px;
    border-radius:100px;
    border:none;
    width:300px;
    transition:width 0.3s;
    outline:none;
}
.resultt p:hover{
    margin-top:10px;
    box-shadow: 0 0 6px  rgb(144, 228, 18);
    text-align: center;
    padding:5px;
    width:400px;
    cursor:pointer;
    border-radius:100px;
    border:none;
    outline:none;
    transition:width 0.3s;
}
.resultt p:Active{
    background-color: greenyellow;
    transition:width 0.3s;
}
