table {
    border-collapse: collapse;
    width: 100%;
    text-align: center;
    justify-items: center;
    
  }
  
  th, td {
    text-align: left;
    padding: 8px;
    margin-top:10px;
  }
  td{
    width: 100%;
    text-align: center;
    margin-top:10px;
    
}
  
  tr{
    color:black;
    width:100%;
    background-color:white;
    
    margin-top:10px;
    
  }
  tr:hover{
    background-color: lightgreen;
  }
  th {
    background-color: #4CAF50;
    color: white;
    text-align: center;
  }